:root {
  --primary: #fff5f5; /* svetlija crvena */
  --text-dark: #000;
}

header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 64px;
  padding: 0 16px;
  background-color: var(--primary);
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1000;
  transition: box-shadow 0.3s ease;
}

header.scrolled {
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.25);
}

.heading {
  font-size: 1.4rem;
  font-weight: 700;
  color: black;
}

/* ===== HAMBURGER BUTTON ===== */
.menu-toggle {
  background: none;
  border: none;
  cursor: pointer;
  padding: 6px;
}

.menu-toggle svg {
  width: 32px;
  height: 32px;
  color: black;
}

/* ===== MOBILE MENU ===== */
header ul {
  position: fixed;
  top: 64px;
  left: 0;
  width: 100%;
  background-color: var(--primary);
  list-style: none;
  margin: 0;
  padding: 12px 0;

  display: flex;
  flex-direction: column;

  /* ANIMATION SETUP */
  transform: translateY(-20px);
  opacity: 0;
  pointer-events: none;

  transition:
    transform 0.35s ease,
    opacity 0.35s ease;
}

/* OPEN STATE */
header ul.open {
  transform: translateY(0);
  opacity: 1;
  pointer-events: auto;
}

/* ===== MENU ITEMS ===== */
header ul li {
  padding: 14px 20px;
  font-size: 1.1rem;
  cursor: pointer;
  color: black;

  opacity: 0;
  transform: translateY(-10px);
  transition:
    opacity 0.3s ease,
    transform 0.3s ease;
}

/* STAGGER EFFECT */
header ul.open li {
  opacity: 1;
  transform: translateY(0);
}

header ul.open li:nth-child(1) {
  transition-delay: 0.05s;
}
header ul.open li:nth-child(2) {
  transition-delay: 0.1s;
}
header ul.open li:nth-child(3) {
  transition-delay: 0.15s;
}

/* ACTIVE FEEDBACK */
header ul li:active {
  background-color: rgba(0, 0, 0, 0.1);
}

/* ===== DESKTOP ===== */
@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }

  header ul {
    position: static;
    flex-direction: row;
    background: none;
    transform: none;
    opacity: 1;
    pointer-events: auto;
    padding: 0;
  }

  header ul li {
    opacity: 1;
    transform: none;
    transition: none;
    padding: 0 20px;
    font-size: 1rem;
  }
}
