/* Osnovni stilovi sekcije */
.contact-section {
  padding: 50px 20px;
  background-color: #fff5f5;
  text-align: center;
  color: #333;
  font-family: "Arial", sans-serif;
}

/* Dostava badge */
.delivery-badge {
  display: inline-block;
  background-color: #fa5b5b;
  color: #fff;
  font-weight: bold;
  padding: 12px 20px;
  border-radius: 30px;
  font-size: 1.2em;
  margin-bottom: 25px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.delivery-badge span {
  display: block;
  font-size: 0.9em;
  color: #fffdd0;
  margin-top: 3px;
}

/* Pulse animacija */
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

/* Naslov i tekst */
.contact-title {
  font-size: 2.8em;
  margin: 20px 0;
  color: #fa5b5b;
}

.contact-text {
  font-size: 1.2em;
  line-height: 1.6;
  margin-bottom: 40px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  color: #555;
}

/* Kartice za kontakt */
.contact-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 25px;
}

.contact-card {
  background-color: #fff;
  padding: 20px 25px;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  width: 220px;
  text-align: center;
  transition:
    transform 0.3s ease,
    box-shadow 0.3s ease;
}

.contact-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

/* Ikone */
.contact-icon {
  font-size: 36px;
  color: #fa5b5b;
  margin-bottom: 10px;
}

.contact-card h3 {
  font-size: 1.2em;
  margin-bottom: 8px;
  color: #333;
}

.contact-card span,
.contact-card a {
  display: block;
  font-size: 1em;
  color: #555;
  text-decoration: none;
  margin-bottom: 4px;
}

/* Instagram posebno */
.insta {
  font-size: 40px;
  color: #fa5b5b;
}

.instagram-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: inherit;
  text-decoration: none;
}

/* On hover, ni≈°ta se ne menja */
.instagram-link:hover {
  color: inherit;
  text-decoration: none;
}

/* Media queries */
@media screen and (max-width: 768px) {
  .contact-title {
    font-size: 2.2em;
  }
  .contact-text {
    font-size: 1.1em;
  }
  .contact-card {
    width: 45%;
  }
}

@media screen and (max-width: 576px) {
  .contact-title {
    font-size: 1.8em;
  }
  .contact-text {
    font-size: 1em;
  }
  .contact-card {
    width: 90%;
  }
  .delivery-badge {
    font-size: 1em;
    padding: 10px 18px;
  }
  .delivery-badge span {
    font-size: 0.8em;
  }
}
