* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    sans-serif;
  background: #f6f6f6;
  color: #222;
}

/* ===== MENU CONTAINER ===== */
.menu {
  max-width: 640px;
  margin: 0 auto;
  padding: 16px;
}

/* ===== HEADINGS ===== */
.menu-heading {
  margin-top: 90px;
  text-align: center;
  font-size: 2.2rem;
  font-weight: 700;
}

.menu-subheading {
  text-align: center;
  font-size: 1.4rem;
  margin-bottom: 24px;
  color: #555;
}

.menu-category {
  font-size: 1.4rem;
  margin: 32px 0 12px;
  border-bottom: 2px solid #fa5b5b;
  padding-bottom: 6px;
}

/* ===== ITEMS ===== */
.menu-items {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-item {
  background: #fff;
  border-radius: 12px;
  padding: 14px 16px;
  margin-bottom: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);
}

/* ime + cena */
.item-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
}

.item-name {
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.3;
}

.item-price {
  font-weight: 700;
  color: #fa5b5b !important;
  white-space: nowrap;
}

/* opis */
.item-description {
  margin-top: 6px;
  font-size: 0.85rem;
  color: #666;
  line-height: 1.4;
}

/* ===== SMALL PHONES ===== */
@media (max-width: 480px) {
  .menu-heading {
    font-size: 1.8rem;
  }

  .menu-category {
    font-size: 1.2rem;
  }

  .item-name {
    font-size: 0.95rem;
  }

  .item-price {
    font-size: 0.95rem;
  }
}
